export const paramsModule = {
  outstock_sn: '',
  customer_id: "",
  company_id: localStorage.getItem('company_id'),
  company_name: localStorage.getItem('company_name'),
  auditor_status: 0,
  is_erp: '',
  pay_status: 0,
  business_start_time: '',
  business_end_time: ''
}

export const rowParams = {
  goods_num: ''
}
export const baseMinxis = {
  data() {
    return {
      footerData: {},
      tableColumn: [
        // {
        //   type: "checkbox",
        //   width: 44,
        //   align: 'center'
        // },
        {
          title: '销售组织',
          field: 'company_name',
          width: 160
        },
        {
          title: '单据号',
          field: 'outstock_sn',
          width: 160,
          slots: {
            default: ({ row }) => {
              return [
                <el-link disabled={this.authority.b_info != 1} onClick={() => { this.goDetail(row) }} >  {row.outstock_sn} </el-link>,
              ]
            }
          }
        },
        {
          title: '客户名称',
          field: 'customer_name',
          width: 80
        },
        {
          field: 'name',
          title: '操作',
          width: 180,
          align: 'center',
          fixed: "right",
          slots: {
            default: ({ row }) => {
              return [
                <el-button size="small" type="text" disabled={this.authority.b_price != 1 || row.pay_status === '完成付款' || row.pay_status === "已作废"} onClick={() => { this.openCollection(row) }} >  收款 </el-button>,
                <el-button size="small" type="text" disabled={this.authority.b_pass != 1 || row.add_status == '手动创建' || row.auditor_status === '已出库' || row.auditor_status === "作废"} onClick={() => { this.add(row) }} >  出库 </el-button>,
                <el-button size="small" type="text" disabled={this.authority.b_invalid != 1 || row.auditor_status === "作废"} onClick={() => { this.invalid(row) }} >  作废 </el-button>,
                <el-button size="small" type="text" disabled={this.authority.b_print != 1} onClick={() => { this.print(row) }} >  打印 </el-button>
              ]
            }
          }
        },
        {
          title: '出库数量',
          field: 'outstock_num',
          headerAlign: "right",
          align: "right",
          width: 84
        },
        {
          title: '优惠金额',
          field: 'discount_total_amount',
          headerAlign: "right",
          align: "right",
          width: 84
        },
        {
          title: '应收金额',
          field: 'settlement_price',
          headerAlign: "right",
          align: "right",
          width: 84
        },
        {
          title: '已收金额',
          field: 'pay_price',
          headerAlign: "right",
          align: "right",
          width: 84
        },
        {
          title: '单据状态',
          field: 'auditor_status',
          width: 84
        },
        {
          title: '单据类型',
          field: 'add_status',
          width: 84
        },
        {
          title: '结算状态',
          field: 'pay_status',
          width: 84
        },
        {
          title: '单据时间',
          field: 'add_time_name',
          width: 160,
          formatter: ({ row }) => {
            return this.$publicFution.getAllTime(row.add_time * 1000)
          },
        },
        {
          title: '备注',
          field: 'abstract'
        }
      ]
    }
  },
  methods: {
    openCollection(row) {
      this.currentRow = row
      this.isShowCollection = true
    },
    dealData(data) {
      if (data.data.code === 200) {
        this.footerData = data.data.data.desc;
      }
    },
    initParams() {
      if (this.$route.params && this.$route.params.type) {
        this.params.auditor_status = 1;
        this.onSearch(1);
      }
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return "页小计";
          }
          if (["settlement_price", "outstock_num", "pay_price", "discount_total_amount"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "outstock_num") {
              return this.$publicFution.priceFormatNone(num, 0)
            } else {
              return this.$publicFution.priceFormatNone(num)
            }
          }
          return null;
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return "合计";
          }
          var num = this.$utils.sum(data, column.property);
          if (column.property === "outstock_num" && this.footerData.total_num !== undefined) {
            return this.$publicFution.priceFormatNone(num, 0)
          }
          if (column.property === "settlement_price" && this.footerData.total_settlement_price !== undefined) {
            return this.$publicFution.priceFormatNone(num, 2)
          }
          if (column.property === "pay_price" && this.footerData.total_pay_price !== undefined) {
            return this.$publicFution.priceFormatNone(num, 2)
          }
          if (column.property === "discount_total_amount" && this.footerData.total_discount_total_amount !== undefined) {
            return this.$publicFution.priceFormatNone(num, 2)
          }
          if (["settlement_price", "outstock_num", "pay_price", "discount_total_amount"].includes(column.property)) {
            return '加载中...'
          }
          return null;
        })
      ]
    }
  },
}