<template>
  <div class="companyGoods_base">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="组织名称" size="mini">
            <getServiceCompany v-model="params.company_id" :fatherParams='params' />
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.outstock_sn" placeholder="请输入" @keyup.enter.native="onSearch" clearable></el-input>
          </el-form-item>
          <el-form-item label="选择客户" size="mini">
            <getServiceUser v-model="params.customer_id" />
          </el-form-item>
          <el-form-item label="单据状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="已出库" :value="2"></el-option>
              <el-option label="未出库" :value="1"></el-option>
              <el-option label="作废" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据类型" size="mini">
            <el-select clearable v-model="params.is_erp" placeholder="请选择">
              <el-option label="全部" value=""></el-option>
              <el-option label="手动创建" :value="2"></el-option>
              <el-option label="小程序推送" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="结算状态" size="mini">
            <el-select clearable v-model="params.pay_status" placeholder="请选择">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="挂账" :value="1"></el-option>
              <el-option label="完成付款" :value="2"></el-option>
              <el-option label="已作废" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button type="primary" @click="onSearch" size="mini">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <!-- v-if="authority.b_add == 1"  -->
        <el-button icon="el-icon-circle-plus-outline" type="primary" v-if="authority.b_add == 1" @click="add" class="addBtn" size="mini">新增
        </el-button>
      </div>
    </div>
    <div class="add_delet"></div>
    <div class="table_container">
      <xTable :ajax='{ url: "serviceStationGetSalesOutList", params: params }' @getData='dealData' :footerMethod='footerMethod' ref='xTable' :tableColumn='tableColumn' />
    </div>
    <collection v-model="isShowCollection" :data='currentRow' @success='onSearch'></collection>
    <lodopDelivery ref="lodopDelivery" title="serviceOutbound" @Search="onSearch"></lodopDelivery>
  </div>
</template>

<script>
import { ThisScope } from "@/utils/commonCrud/CRUD.js";
import { baseMinxis, paramsModule } from "./config/outboundList";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //出库单打印组件

import collection from "./config/collection";
export default {
  name: "service_station_sales_outstock_list",
  mixins: [baseMinxis],
  data() {
    return {
      isShowCollection: false,
      currentRow: {},
      multipleSelection: [],
      params: this.$publicFution.deepCopy(paramsModule),
      timeValue: [],
    };
  },
  components: {
    collection,
    lodopDelivery,
  },
  methods: {
    print(row) {
      this.$refs.lodopDelivery.printPdf({
        outstock_id: row.outstock_id,
      });
    },
    invalid(row) {
      this.multipleSelection = [row];
      new ThisScope({
        id: "outstock_id",
        _this: this,
        allTitle: "是否要作废销售出库单，并同时作废该出库单的已收金额？",
        url: "serviceStationaBolishSalesOut",
        showConfirm: true,
      }).send();
    },
    goDetail(row) {
      this.$router.push({
        path: "/sales/service_station_sales_outstock_list/service_station_sales_outstock_see",
        query: {
          id: row.outstock_id,
        },
      });
    },
    add(row) {
      this.$router.push({
        path: "/sales/service_station_sales_outstock_list/service_station_sales_outstock_add",
        query: {
          id: row.outstock_id,
        },
      });
    },
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.timeValue = [];
      this.onSearch(1);
    },
    // 删除
    deletes() {
      new ThisScope({
        id: "inventory_goods_id",
        _this: this,
        title: "删除",
        url: "serviceStationDelCompanyGoods",
        showConfirm: true,
      }).send();
    },
    // 导出
    exportOrder(scheduleType) {
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$api.serviceStationExportGoodsUrl(s).then((res) => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    onSearch(num) {
      if (Array.isArray(this.timeValue)) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = "";
        this.params.business_end_time = "";
      }
      this.$refs.xTable._getData(num);
    },
  },
  activated() {
    this.initParams();
  },
};
</script>

<style scoped>
.el-dropdown {
  margin: 0 9px;
}
</style>
