<template>
  <div class="params">
    <vxe-modal v-model="value" title='销售出库收款' @hide="close" width="800" :esc-closable='true' destroy-on-close height="300" min-width="600" min-height="100" resize remember storage transfer>
      <div>
        <el-row>
          <el-col :span='12'>
            销售组织： {{ data.company_name }}
          </el-col>
          <el-col :span='12'>
            单据号： {{ data.outstock_sn }}
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            客户名称： {{ data.customer_name }}
          </el-col>
          <el-col :span='12'>
            出库数量： {{ data.outstock_num }}
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            已收金额： {{ data.pay_price }}
          </el-col>
        </el-row>
        <div class='line'>
        </div>
        <div>
          本次收款：
          <span>
            <el-input v-model="pay_price" style='width: 100px;' @change="payPriceChange">
              <span slot="suffix" class="el-input__icon">元</span>
            </el-input>
          </span>
          &nbsp;&nbsp;&nbsp;
          <span>欠款： {{ (data.settlement_price -  data.pay_price).toFixed(2) }} 元</span>
        </div>
      </div>
      <div class='line' style="margin-top: 40px;">
      </div>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit">确定</el-button>
        <el-button @click="close">取 消</el-button>
      </div>

    </vxe-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pay_price: "",
    };
  },
  // watch: {
  //   value(newVal) {
  //     if (newVal === true) {
  //       this.getData();
  //     }
  //   },
  // },
  props: {
    value: null,
    data: {
      type: Object,
      default: () => {},
    },
  },
  methods: {
    payPriceChange() {
      if (this.pay_price < 0) {
        this.$message.warning("本次收款不能小于0");
        this.pay_price = "";
      }
      if (this.pay_price > this.data.settlement_price - this.data.pay_price) {
        this.$message.warning("本次收款不能大于总金额");
        this.pay_price = "";
      }
    },
    close() {
      this.$emit("input", false);
    },
    submit() {
      if (this.pay_price >= 0 && this.pay_price != "") {
        this.$api
          .serviceStationSalesOutPayPrice({
            outstock_id: this.data.outstock_id,
            pay_price: this.pay_price,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              this.$message.success(data.msg);
              this.$emit("success");
              this.close();
            }
          });
      }
    },
  },
};
</script>

<style scoped lang='less'>
.line {
  height: 1px;
  margin: 10px 0px 10px 0px;
  background-color: rgba(0, 0, 0, 0.1);
}
</style>